
export default function yadev_main_swiper(args = {}){
  


      const init_swiper = ()=> {
        var swiper = new Swiper('.swiper-container', {

            direction: 'vertical',
            mousewheelControl: true,
            slidesPerView: 1,
            // freeMode: true,
            // freeModeSticky: true
          });
      }



      document.addEventListener('DOMContentLoaded', init_swiper);
}